.page-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include mdx($tablet-width) {
    min-height: calc(100vh - 90px);
    margin-bottom: $padGeneral*2;
  }

  @include mdn($tablet-width + 1) {
    min-height: calc(100vh - 120px);
    //padding-top: 40px;
    //padding-bottom: 40px;
  }

  &__list-wrapper {
    //display: flex;
    //justify-content: space-between;

    //flex-basis: 30%;
  }

  &__list {
    list-style: none;
    max-width: 660px;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;

    @include mdx($tablet-width) {
      flex-direction: column;
      align-items: stretch;
    }

    @include mdn($tablet-width + 1) {
      flex-direction: row;
      align-items: flex-end;
      padding: 40px 0;
      width: 100%;
    }
  }

  &__item {
    @include mdx($tablet-width) {
      &:not(:last-child) {
        margin-bottom: $padGeneral*2;
      }
    }

    /* @include mdn($tablet-width + 1) {
      padding-top: 20px;
      padding-bottom: 20px;
    } */
  }

  &__link {
    display: block;
    color: $colorTextLinkFooter;
    font-size: 18px;
    line-height: $lh;
    text-decoration: none;
    text-align: center;

    // @include mdx($tablet-width) {}

    // @include mdn($tablet-width + 1) {}
  }
}

.footer-message {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #020202;
  padding: 20px 0;
  text-align: center;
  font-size: $fsFooterMessage;
  color: $colorFooterMessage;

  z-index: 4;

  //@include mdx($tablet-width) {
  //  position: relative;
  //}

  // @include mdn($tablet-width + 1) {}

  &__container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
  }

  &__text {
    flex-grow: 1;
    text-align: center;
  }

  &__close {
    border: none;
    background-color: transparent;
    width: 35px;
    height: 20px;
    padding: 0 5px;
    cursor: pointer;
    text-align: center;

    path {
      fill: #fff;
    }
  }
}